<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="step++">Next</li>
      <li v-if="step==2" @click="publish">Publish</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container :insta_data="insta_data" :step="step" :url_upload="url_upload"  @write="작성한글 = $event"/>
  <div style="text-align: center">
    <button @click="more" style="text-align: center">
      More {{ more_click }}
    </button>
  </div>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" accpect="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!--
  <div v-if="button0==true">con0</div>
  <div v-if="button1==true">con1</div>
  <div v-if="button2==true">con2</div>
 
  
  <div v-if="step==0">con0</div>
