  <div v-if="step==2">con2</div>

  <button @click="step=0" >b0</button>
  <button @click="step=1"  >b1</button>
  <button @click="step=2">b2</button>
   -->
</template>

<script>
import Container from "./components/Container.vue";
import postdata from "./assets/postdata.js";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      작성한글 : '',
      /*
      button0 : true,
      button1 : false,
      button2 : false,
      */
      url_upload: '',
      step: 0,
      more_click: 0,
      insta_data: postdata,
    };
  },

  components: {
    Container: Container,
  },
  methods: {
    publish(){
      var currentpost = {
         name: "Cy Lee2",
      userImage: "https://placeimg.com/100/100/animals",
      postImage: this.url_upload,
      likes: 59,
      date: "Feb 28, 2023",
      liked: false,
      content: this.작성한글,
      filter: "lofi"
      };
      this.insta_data.unshift(currentpost);
      this.step = 0;

    },

    /*
     b0() {
      this.step=0;
     
      this.button0=true;      
      this.button1=false;
      this.button2=false;
       
    },
    b1() {
      this.step=1;
     
      this.button0=false;      
      this.button1=true;
      this.button2=false;
       
    },
     b2() {
      this.step=2;
      /*
      this.button0=false;      
      this.button1=false;
      this.button2=true;
      
    },
    */
    upload(e) {
      let file = e.target.files
      //console.log(file[0]);
      console.log(file[0].type);
      let url = URL.createObjectURL(file[0]);
      console.log(url);
      this.url_upload = url;
      this.step = 1;
    },
    more() {
      //axios.post('URL',{name:'kim'}).then().catch((err)=>{console.log(err)})

      axios
        .get(`https://codingapple1.github.io/vue/more${this.more_click}.json`)
        //.then(function(result){
        .then((result) => {
          //success
          console.log(result.data);
          this.insta_data.push(result.data);
          this.more_click++;
        });
    },
  },
};
</script>

